body {
  
}

.container {
  height: auto;
  width: 400px;
  background-color:#1e1e1e;
  color: #ffffff;
  margin: auto;
  margin-top: 20px;
  border-radius: 20px;
  padding: 30px;
}

.upper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
  padding:10px;
  padding-bottom: 5;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.hero img {
  height : 50px;
  width:50px;
}

.upper {
  font-size: 25px;
  font-weight: 30;
  display: flex;
  justify-content: center;
  align-items: center;
}

.statBar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 5px ;
}

.stat {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 15%;
  font-size: 15px;
  width:18px;
  height: 18px;
}


.stat img {
  width:18px;
  height: 18px;
}

.Equipment {
  margin: auto;
  margin-top: 15px;
  display: grid;
  width: 300px;
  place-items: center;
  grid-template-columns: repeat(3, 1fr);
}

.item {
  height: 80px;
  width: 80px;
  border: 1px solid white;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}


.components{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

.components button {
   padding: 10px 20px;
    background: #ff0000;
    color: #fff;
    border: none;
    borderRadius: 5px;
    cursor: pointer;
    margin-right: 10px;
}